@import "include-media";

.container {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 24px;
	text-align: left;

	@include media(">=tablet") {
		padding: 0 36px;
	}

	&::after {
		display: none;
	}

	&::before {
		display: none;
	}
}

.link {
	color: #1a265c;
	font-weight: bold;
	font-size: 1em;
	line-height: 1.5;
	text-decoration: underline;

	&.nav {
		text-decoration: none;
	}

	&.light {
		color: #ffffff;
	}

	&.green {
		color: rgb(159, 215, 0);
		white-space: nowrap;
	}
}

.space-between-vertical {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.row-flex {
	&.reverse {
		flex-direction: row-reverse;
	}

	&.nowrap {
		flex-wrap: nowrap;
	}

	&.align-center {
		align-items: center;
	}
	
	&-full {
		display: flex;
		justify-content: center;
		flex-flow: row wrap;
		gap: 24px;

		&>* {
			flex-grow: 1;
		}
	}

	@include media(">=tablet") {
		display: flex;
		flex-flow: row wrap;
		gap: 24px;
		
		.col-6 {
			flex-basis: calc((100% - 24px) / 2);
		}

		.col-5 {
			flex-basis: calc((100% - 24px) / (12 / 6));
			order: 2;
		}

		.col-7 {
			flex-basis: calc((100% - 24px) / (12 /6));
			order: 1;
		}

		.col-12 {
			flex-basis: 100%;
		}

		&>* {
			flex-grow: 1;
		}
	}
	
}